import React from "react";

import ImgChose from "./ImgChose";

import Warm from "../../assets/images/warm.jpg";
import Day from "../../assets/images/day.jpg";
import Cold from "../../assets//images/cold.jpg";


const PickLight = ({ isChosen, img, visibility }) => {
  let { vis1, vis2, vis3 } = visibility;

  if (isChosen === 1) {
    vis1 = "visible";
    vis2 = "invisible";
    vis3 = "invisible";
  } else if (isChosen === 2) {
    vis1 = "invisible";
    vis2 = "visible";
    vis3 = "invisible";
  } else if (isChosen === 3) {
    vis1 = "invisible";
    vis2 = "invisible";
    vis3 = "visible";
  }

  return (
    <div className="container">
      <div className="card-deck d-flex justify-content-center">
        <ImgChose
          visibility={vis1}
          isChosen={isChosen}
          img={img}
          src={Warm}
          title={"Теплый"}
          num={1}
        />
        <ImgChose
          visibility={vis2}
          isChosen={isChosen}
          img={img}
          src={Day}
          title={"Дневной"}
          num={2}
        />
        <ImgChose
          visibility={vis3}
          isChosen={isChosen}
          img={img}
          src={Cold}
          title={"Холодный"}
          num={3}
        />
      </div>
    </div>
  );
};

export default PickLight;
