import React, { Component } from "react";

import Warm from "../../assets/images/warm.jpg";

import ImgContent from "./ImgContent";
import Desc from "./Desc";
import Info from "./Info";
import PickLight from "./PickLight";
import HideText from "./HideText";

export default class MainContent extends Component {
  constructor() {
    super();

    this.state = {
      showText: true,
      imgUrl: Warm,
      isChosen: false,
      visibility: {
        vis1: "visible",
        vis2: "invisible",
        vis3: "invisible"
      }
    };
  }

  changeImg = (img, num) => {
    this.setState({
      imgUrl: img,
      isChosen: num
    });
  };

  showText = () => {
    const { showText } = this.state;
    this.setState({ showText: !showText });
  };

  render() {
    return (
      <div className="d-flex">
        {this.state.showText ? (
          <div className="mainContent d-flex w-100">
            <ImgContent img={this.state.imgUrl} />
            <div className="container border-bottom" style={{ padding: 0 }}>
              <Desc />
              <Info show={this.showText} />
              <PickLight
                visibility={this.state.visibility}
                isChosen={this.state.isChosen}
                img={this.changeImg}
              />
            </div>
          </div>
        ) : (
          <HideText show={this.showText} />
        )}
      </div>
    );
  }
}
