import React from "react";

const ImgChose = ({ src, title, isChosen, img, visibility, num }) => {
  const iconStyle = ["fa", "fa-check-square", "text-right", visibility];
  return (
    <div
      onClick={() => img(src, num)}
      className="card text-center mx-4 mt-5"
      style={{ border: 0, maxWidth: 180, cursor: "pointer" }}
    >
      <img
        src={src}
        alt=""
        className="card-image img-fluid mx-auto rounded"
        style={{ border: 0, padding: 0, maxWidth: 180 }}
      />
      <div className="card-img-overlay d-flex flex-column p-0 justify-content-between">
        <i style={{ color: "#00abff" }} className={iconStyle.join(" ")}></i>
        <h5
          style={{ backgroundColor: "black", color: "white" }}
          className="card-title py-2 m-0"
        >
          {title}
        </h5>
      </div>
    </div>
  );
};

export default ImgChose;
